.place {
  &__photo-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 320px;
    overflow: hidden;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.15);
    }
    
    &--1:hover {
      transform: scale(1.07);
    }
    
    &--7:hover {
      transform: scale(1.07);
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 320px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;

    

    &--1 {
      object-position: 25%;
    }

    &--7 {
      object-position: right;
    }
  }

  &__photos {
    margin-bottom: 24px;
  }

  &__subtitle {
    margin: 0;
    font-weight: bold;
    font-size: 16px;
    line-height: 27px;
    color: $colorAccentMain;
  }

  &__paragraph {
    margin: 0;
    font-size: 16px;
    line-height: 27px;
  }
}
